@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

@keyframes captionRowAnimate {
  from {
    font-size: 0vw;
  }
  to {
    font-size: 2vw;
  }
}

@keyframes valueRowAnimate {
  from {
    font-size: 0vw;
  }
  to {
    font-size: 4vw;
  }
}

@keyframes valueRowAnimateBanner {
  from {
    font-size: 0vw;
  }
  to {
    font-size: 3vw;
  }
}

@keyframes captionRowAnimateMobile {
  from {
    font-size: 0vh;
  }
  to {
    font-size: 2vh;
  }
}

@keyframes valueRowAnimateMobile {
  from {
    font-size: 0vh;
  }
  to {
    font-size: 4vh;
  }
}

.numbers {
  display: flex;
  overflow-wrap: normal;
  margin-left: 0;
  margin-right: 0;
  margin-top: 5;
  width: 100vw;
  height: 25vh;
  min-height: 10vw;
}

.column {
  color: #ffffff;
  width: 33%;
  font-size: 4vw;
  animation-name: valueRowAnimate;
  animation-duration: 2s;
  font-weight: bold;
  margin-left: 0;
  margin-right: 0;
  margin-top: 2;
}

.caption {
  color: #ed8b00;
  animation-name: captionRowAnimate;
  animation-duration: 2s;
  font-size: 2vw;
  font-weight: 500;
}

.heading {
  display: flex;
  margin-left: 0;
  margin-right: 0;
  font-weight: 500;
  font-size: 4vw;
  font-family: "UniNeueBlack", "Roboto", sans-serif;
  animation-name: valueRowAnimate;
  animation-duration: 2s;
  min-height: 4vw;
}
.heading > div {
  color: #ffffff;
  text-align: center;
  width: 100vw;
}

/* Style the video: 100% width and height to cover the entire window */
.myVideo {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  min-width: 100vw;
  min-height: 100vh;
  overflow: hidden;
  object-fit: cover;
  background-color: #2d32aa;
}

/* Add some content at the bottom of the video/page */
.content {
  position: fixed;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.logoImageHolder {
  position: fixed;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
}

.logoImage {
  position: fixed;
  top: 90vh;
  display: flex;
  justify-content: right;
  width: 100vw;
  height: 50vh;
  min-height: 500px;
}

.logoImage > img {
  height: 6vh;
  margin-right: 5px;
}
/*Styling for mobile*/
@media (max-aspect-ratio: 9/10) {
  .numbers {
    flex-direction: column;
    justify-content: center;
    width: 100vw;
  }

  .column {
    width: 100vw;
    font-size: 4vh;
    animation-name: valueRowAnimateMobile;
    animation-duration: 2s;
  }

  .caption {
    font-size: 2vh;
    animation-name: captionRowAnimateMobile;
    animation-duration: 2s;
  }

  .heading {
    font-size: 4vh;
    animation-name: valueRowAnimateMobile;
    animation-duration: 2s;
  }

  .logoImage > img {
    height: 6vw;
  }
}

.App {
  text-align: center;
  background-color: #2d32aa;
}

/*Styling for banner on Intranets*/
@media (max-height: 350px) {
  .myVideo {
    position: fixed;
    display: flex;
    justify-content: start;
    top: 0;
    left: 0;
    height: auto;
    width: 100vw;
    min-width: 100vw;
    min-height: 0px;
    overflow: hidden;
    object-fit: cover;
    background-color: #2d32aa;
  }

  .column {
    font-size: 3vw;
    animation-name: valueRowAnimateBanner;
    animation-duration: 2s;
  }

  .caption {
    color: #ed8b00;
    animation-name: captionRowAnimate;
    animation-duration: 2s;
    font-size: 2vw;
    font-weight: 500;
  }

  .heading {
    font-size: 3vw;
    animation-name: valueRowAnimateBanner;
    animation-duration: 2s;
    min-height: 3vw;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
