@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@charset "UTF-8";
body {
  margin: 0;
  background-color: #2d32aa;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes captionRowAnimate {
  from {
    font-size: 0vw;
  }
  to {
    font-size: 2vw;
  }
}

@keyframes captionRowAnimate {
  from {
    font-size: 0vw;
  }
  to {
    font-size: 2vw;
  }
}

@-webkit-keyframes valueRowAnimate {
  from {
    font-size: 0vw;
  }
  to {
    font-size: 4vw;
  }
}

@keyframes valueRowAnimate {
  from {
    font-size: 0vw;
  }
  to {
    font-size: 4vw;
  }
}

@-webkit-keyframes valueRowAnimateBanner {
  from {
    font-size: 0vw;
  }
  to {
    font-size: 3vw;
  }
}

@keyframes valueRowAnimateBanner {
  from {
    font-size: 0vw;
  }
  to {
    font-size: 3vw;
  }
}

@-webkit-keyframes captionRowAnimateMobile {
  from {
    font-size: 0vh;
  }
  to {
    font-size: 2vh;
  }
}

@keyframes captionRowAnimateMobile {
  from {
    font-size: 0vh;
  }
  to {
    font-size: 2vh;
  }
}

@-webkit-keyframes valueRowAnimateMobile {
  from {
    font-size: 0vh;
  }
  to {
    font-size: 4vh;
  }
}

@keyframes valueRowAnimateMobile {
  from {
    font-size: 0vh;
  }
  to {
    font-size: 4vh;
  }
}

.numbers {
  display: flex;
  overflow-wrap: normal;
  margin-left: 0;
  margin-right: 0;
  margin-top: 5;
  width: 100vw;
  height: 25vh;
  min-height: 10vw;
}

.column {
  color: #ffffff;
  width: 33%;
  font-size: 4vw;
  -webkit-animation-name: valueRowAnimate;
          animation-name: valueRowAnimate;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  font-weight: bold;
  margin-left: 0;
  margin-right: 0;
  margin-top: 2;
}

.caption {
  color: #ed8b00;
  -webkit-animation-name: captionRowAnimate;
          animation-name: captionRowAnimate;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  font-size: 2vw;
  font-weight: 500;
}

.heading {
  display: flex;
  margin-left: 0;
  margin-right: 0;
  font-weight: 500;
  font-size: 4vw;
  font-family: "UniNeueBlack", "Roboto", sans-serif;
  -webkit-animation-name: valueRowAnimate;
          animation-name: valueRowAnimate;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  min-height: 4vw;
}
.heading > div {
  color: #ffffff;
  text-align: center;
  width: 100vw;
}

/* Style the video: 100% width and height to cover the entire window */
.myVideo {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  min-width: 100vw;
  min-height: 100vh;
  overflow: hidden;
  object-fit: cover;
  background-color: #2d32aa;
}

/* Add some content at the bottom of the video/page */
.content {
  position: fixed;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.logoImageHolder {
  position: fixed;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
}

.logoImage {
  position: fixed;
  top: 90vh;
  display: flex;
  justify-content: right;
  width: 100vw;
  height: 50vh;
  min-height: 500px;
}

.logoImage > img {
  height: 6vh;
  margin-right: 5px;
}
/*Styling for mobile*/
@media (max-aspect-ratio: 9/10) {
  .numbers {
    flex-direction: column;
    justify-content: center;
    width: 100vw;
  }

  .column {
    width: 100vw;
    font-size: 4vh;
    -webkit-animation-name: valueRowAnimateMobile;
            animation-name: valueRowAnimateMobile;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
  }

  .caption {
    font-size: 2vh;
    -webkit-animation-name: captionRowAnimateMobile;
            animation-name: captionRowAnimateMobile;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
  }

  .heading {
    font-size: 4vh;
    -webkit-animation-name: valueRowAnimateMobile;
            animation-name: valueRowAnimateMobile;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
  }

  .logoImage > img {
    height: 6vw;
  }
}

.App {
  text-align: center;
  background-color: #2d32aa;
}

/*Styling for banner on Intranets*/
@media (max-height: 350px) {
  .myVideo {
    position: fixed;
    display: flex;
    justify-content: start;
    top: 0;
    left: 0;
    height: auto;
    width: 100vw;
    min-width: 100vw;
    min-height: 0px;
    overflow: hidden;
    object-fit: cover;
    background-color: #2d32aa;
  }

  .column {
    font-size: 3vw;
    -webkit-animation-name: valueRowAnimateBanner;
            animation-name: valueRowAnimateBanner;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
  }

  .caption {
    color: #ed8b00;
    -webkit-animation-name: captionRowAnimate;
            animation-name: captionRowAnimate;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    font-size: 2vw;
    font-weight: 500;
  }

  .heading {
    font-size: 3vw;
    -webkit-animation-name: valueRowAnimateBanner;
            animation-name: valueRowAnimateBanner;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    min-height: 3vw;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.loading-spinner {
  width: 50px;
  height: 50px;
  border: 10px solid #f3f3f3; /* Light grey */
  border-top: 10px solid #383636; /* Blue */
  border-radius: 50%;
  -webkit-animation: spinner 1.5s linear infinite;
          animation: spinner 1.5s linear infinite;
}

.spinner-container {
  align-items: center;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  top: 40%;
  left: 0;
  height: auto;
  width: 100vw;
  min-width: 100vw;
}

